/* Base styles */
body {
  font-family: 'Inter', 'Poppins', system-ui, sans-serif;
  background: linear-gradient(135deg, #051A05 0%, #0A2A0A 50%, #051A05 100%);
  color: white;
  min-height: 100vh;
}

/* Smooth scrolling */
html {
  scroll-behavior: smooth;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #051A05;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #00FFC2, #005533);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #00FFC2, #D1FFE6);
}

/* Custom component classes */

/* Page transition animations */
.page-enter {
  opacity: 0;
  transform: translateY(20px);
}

.page-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 300ms, transform 300ms;
}

.page-exit {
  opacity: 1;
  transform: translateY(0);
}

.page-exit-active {
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 300ms, transform 300ms;
}

/* Loading spinner */
.spinner{
  height: 1.5rem;
  width: 1.5rem;
}
@keyframes spin{
  to{
    transform: rotate(360deg);
  }
}
.spinner{
  animation: spin 1s linear infinite;
  border-radius: 9999px;
  border-bottom-width: 2px;
  --tw-border-opacity: 1;
  border-color: rgb(0 255 194 / var(--tw-border-opacity, 1));
}

